import React from "react";
import ReactDOM from "react-dom";
import Popup from "reactjs-popup";
import { nanoid } from "nanoid";
import refresh from "./refresh.svg";

const preFill = [
  {
    firstName: "Marko",
    lastName: "Horvat",
    address: "Ilica 87, 10000 Zagreb",
    phoneNumber: 123456789,
    id: nanoid(3),
  },
  {
    firstName: "Kristina",
    lastName: "Novoselec",
    address: "Vukovarska 136, 10000 Zagreb",
    phoneNumber: 987654321,
    id: nanoid(3),
  },
  {
    firstName: "Lorena",
    lastName: "PavloviÄ‡",
    address: "Zvonimirova 15, 21000 Split",
    phoneNumber: 543219876,
    id: nanoid(3),
  },
];
const popup = {
  borderWidth: 2,
  borderStyle: "solid",
  borderColor: "black",
  background: "lavender",
};
const popupBtns = {
  display: "flex",
  justifyContent: "center",
  flexDirection: "row",
};
const input = {
  marginTop: 80,
  display: "flex",
  justifyContent: "center",
  flexDirection: "column",
};
const inputField = {
  margin: 20,
  fontSize: 25,
  height: 40,
  width: 400,
  alignSelf: "center",
  paddingLeft: 10,
};
const inputForm = {
  fontSize: 20,
};
const inputBtn = {
  margin: 20,
  fontSize: 30,
  height: 60,
  width: 250,
};
const dataField = {
  margin: 20,
  fontSize: 25,
  height: 40,
  width: 300,
  alignSelf: "center",
  paddingLeft: 10,
  borderWidth: 2,
  borderStyle: "solid",
  borderColor: "black",
  background: "FloralWhite",
};
const display = {
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
};
const utillity = {
  display: "flex",
  flexDirection: "row",
  alignItems: "center",
};
const deleteBtn = {
  margin: 20,
  fontSize: 30,
  height: 40,
  width: 40,
  borderWidth: 2,
  borderStyle: "solid",
  borderColor: "black",
  background: "LightCoral",
};
const icon = {
  width: 30,
  height: 30,
  background: "LightCoral",
};

class App extends React.Component {
  constructor(props) {
    super(props);

    let peoplesList = [];
    if (localStorage.getItem("peoplesList") !== null) {
      peoplesList = JSON.parse(localStorage.getItem("peoplesList"));
    } else {
      peoplesList = preFill;
    }
    this.state = {
      peoplesList: peoplesList,
      firstName: "",
      lastName: "",
      address: "",
      phoneNumber: "",
      search: "",
    };

    this.addNewPersonToList = this.addNewPersonToList.bind(this);
    this.deleteAllData = this.deleteAllData.bind(this);
    this.allowNumberInput = this.allowNumberInput.bind(this);
    this.dataSearch = this.dataSearch.bind(this);
    this.sortPeopleData = this.sortPeopleData.bind(this);
  }

  allowNumberInput(event) {
    const phoneNumber = event.target.value;
    const regex = /([0-9-+/\b])+$/;
    if (event.target.value === "" || regex.test(event.target.value)) {
      this.setState({ phoneNumber: phoneNumber });
    }
  }

  addNewPersonToList = (event) => {
    const allPersons = [...this.state.peoplesList];
    if (
      this.state.firstName === "" &&
      this.state.lastName === "" &&
      this.state.address === "" &&
      this.state.phoneNumber === ""
    ) {
      this.setState({
        peoplesList: allPersons,
      });
    } else {
      this.setState(
        {
          peoplesList: [
            {
              firstName: this.state.firstName,
              lastName: this.state.lastName,
              address: this.state.address,
              phoneNumber: this.state.phoneNumber,
              id: nanoid(3),
            },
            ...allPersons,
          ],
          firstName: "",
          lastName: "",
          address: "",
          phoneNumber: "",
        },
        () => {
          localStorage.setItem(
            "peoplesList",
            JSON.stringify(this.state.peoplesList)
          );
        }
      );
    }
    event.preventDefault();
  };

  deleteAllData() {
    this.setState(
      {
        peoplesList: [],
      },
      () => {
        localStorage.clear("peoplesList");
      }
    );
  }

  deleteDataRow(id) {
    const newPeoplesList = [...this.state.peoplesList].filter(
      (person) => person.id !== id
    );
    this.setState(
      {
        peoplesList: [...newPeoplesList],
      },
      () => {
        localStorage.setItem("peoplesList", JSON.stringify(newPeoplesList));
      }
    );
  }

  updatePersonData(person, dataType, value) {
    const updateData = this.state.peoplesList.map((i) =>
      i.id === person.id
        ? {
            ...i,
            [dataType]: value,
          }
        : i
    );

    this.setState(
      {
        peoplesList: updateData,
      },
      () => {
        localStorage.setItem(
          "peoplesList",
          JSON.stringify(this.state.peoplesList)
        );
      }
    );
  }

  sortPeopleData(event) {
    const column = event.target.value;
    if (this.state.sortDirection === "ascending") {
      if (column !== "phoneNumber") {
        const sortedList = [].concat(this.state.peoplesList).sort((a, b) => {
          const nameA = a[column].toLowerCase();
          const nameB = b[column].toLowerCase();
          if (nameA > nameB) {
            return -1;
          }
          if (nameA < nameB) {
            return 1;
          }
          return 0;
        });
        this.setState({
          sortDirection: "descending",
          peoplesList: [...sortedList],
        });
      } else {
        const sortedList = [].concat(this.state.peoplesList).sort((a, b) => {
          const nameA = a[column];
          const nameB = b[column];
          if (nameA > nameB) {
            return -1;
          }
          if (nameA < nameB) {
            return 1;
          }
          return 0;
        });
        this.setState({
          sortDirection: "descending",
          peoplesList: [...sortedList],
        });
      }
    } else {
      if (column !== "phoneNumber") {
        const column = event.target.value;
        const sortedList = [].concat(this.state.peoplesList).sort((a, b) => {
          const nameA = a[column].toLowerCase();
          const nameB = b[column].toLowerCase();
          if (nameA < nameB) {
            return -1;
          }
          if (nameA > nameB) {
            return 1;
          }
          return 0;
        });
        this.setState({
          sortDirection: "ascending",
          peoplesList: [...sortedList],
        });
      } else {
        const column = event.target.value;
        const sortedList = [].concat(this.state.peoplesList).sort((a, b) => {
          const nameA = a[column];
          const nameB = b[column];
          if (nameA < nameB) {
            return -1;
          }
          if (nameA > nameB) {
            return 1;
          }
          return 0;
        });
        this.setState({
          sortDirection: "ascending",
          peoplesList: [...sortedList],
        });
      }
    }
  }

  dataSearch(event) {
    this.setState({
      search: event.target.value,
    });
  }

  refreshState() {
    this.setState({
      ...this.state.peoplesList,
      search: "",
    });
  }

  render() {
    const filteredPeople = this.state.peoplesList.filter((person) => {
      return (
        person.firstName
          .toLowerCase()
          .indexOf(this.state.search.toLowerCase()) !== -1 ||
        person.lastName
          .toLowerCase()
          .indexOf(this.state.search.toLowerCase()) !== -1
      );
    });
    return (
      <div style={display}>
        <div style={utillity}>
          <Popup trigger={<button style={inputBtn}>New Entry</button>} modal>
            {(close) => (
              <div style={popup}>
                <div>
                  <form style={input}>
                    <input
                      style={inputField}
                      type="text"
                      value={this.state.firstName}
                      onChange={(event) =>
                        this.setState({ firstName: event.target.value })
                      }
                      autoComplete="off"
                      placeholder="Enter First Name"
                    ></input>
                    <input
                      style={inputField}
                      type="text"
                      value={this.state.lastName}
                      onChange={(event) =>
                        this.setState({ lastName: event.target.value })
                      }
                      autoComplete="off"
                      placeholder="Enter Last Name"
                    ></input>
                    <input
                      style={inputField}
                      type="text"
                      value={this.state.address}
                      onChange={(event) =>
                        this.setState({ address: event.target.value })
                      }
                      autoComplete="off"
                      placeholder="Enter Address"
                    ></input>
                    <input
                      style={inputField}
                      type="text"
                      value={this.state.phoneNumber}
                      onChange={this.allowNumberInput}
                      autoComplete="off"
                      placeholder="Enter Phone Number"
                    ></input>
                    <div style={popupBtns}>
                      <button
                        style={inputBtn}
                        onClick={this.addNewPersonToList}
                      >
                        Submit
                      </button>
                      <button
                        style={inputBtn}
                        onClick={() => {
                          close();
                        }}
                      >
                        Close Window
                      </button>
                    </div>
                  </form>
                </div>
              </div>
            )}
          </Popup>
          <button style={inputBtn} onClick={this.deleteAllData}>
            Clear All Data
          </button>
          <input
            style={dataField}
            type="text"
            id="filter"
            placeholder="Search for..."
            onChange={this.dataSearch}
            value={this.state.search}
            autoComplete="off"
          />
          <button style={deleteBtn} onClick={this.refreshState.bind(this)}>
            <img style={icon} src={refresh} alt="refresh" />
          </button>
        </div>

        <div style={inputForm}>
          <form>
            <table>
              <thead>
                <tr>
                  <th>
                    <button
                      style={inputBtn}
                      type="button"
                      onClick={this.sortPeopleData}
                      value={"firstName"}
                    >
                      First Name
                    </button>
                  </th>
                  <th>
                    <button
                      style={inputBtn}
                      type="button"
                      onClick={this.sortPeopleData}
                      value={"lastName"}
                    >
                      Second Name
                    </button>
                  </th>
                  <th>
                    <button
                      style={inputBtn}
                      type="button"
                      onClick={this.sortPeopleData}
                      value={"address"}
                    >
                      Address
                    </button>
                  </th>
                  <th>
                    <button
                      style={inputBtn}
                      type="button"
                      onClick={this.sortPeopleData}
                      value={"phoneNumber"}
                    >
                      Phone Number
                    </button>
                  </th>
                </tr>
              </thead>
              {filteredPeople.map((person) => {
                return (
                  <tbody key={person.id}>
                    <tr>
                      <td>
                        <input
                          style={dataField}
                          onChange={(e) =>
                            this.updatePersonData(
                              person,
                              "firstName",
                              e.target.value
                            )
                          }
                          type="text"
                          placeholder="no data"
                          value={person.firstName}
                        />
                      </td>
                      <td>
                        <input
                          style={dataField}
                          onChange={(e) =>
                            this.updatePersonData(
                              person,
                              "lastName",
                              e.target.value
                            )
                          }
                          type="text"
                          placeholder="no data"
                          value={person.lastName}
                        />
                      </td>
                      <td>
                        <input
                          style={dataField}
                          onChange={(e) =>
                            this.updatePersonData(
                              person,
                              "address",
                              e.target.value
                            )
                          }
                          type="text"
                          placeholder="no data"
                          value={person.address}
                        />
                      </td>
                      <td>
                        <input
                          style={dataField}
                          onChange={(e) =>
                            this.updatePersonData(
                              person,
                              "phoneNumber",
                              e.target.value
                            )
                          }
                          type="text"
                          placeholder="no data"
                          value={person.phoneNumber}
                        />
                      </td>
                      <td>
                        <button
                          style={deleteBtn}
                          onClick={() => {
                            this.deleteDataRow(person.id);
                          }}
                        >
                          x
                        </button>
                      </td>
                    </tr>
                  </tbody>
                );
              })}
            </table>
          </form>
        </div>
      </div>
    );
  }
}

ReactDOM.render(<App />, document.getElementById("root"));
